import { createWebHistory, createRouter } from "vue-router";
import HomeView from "@/views/HomeView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/course",
    name: "course list",
    component: () => import('@/views/CourseList.vue'),
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;